/**
 * switch 开关选择器
 * @description 选择开关一般用于只有两个选择，且只能选其一的场景。
 * @tutorial https://www.uviewui.com/components/switch.html
 * @property {Boolean} loading 是否处于加载中（默认false）
 * @property {Boolean} disabled 是否禁用（默认false）
 * @property {String Number} size 开关尺寸，单位rpx（默认50）
 * @property {String} active-color 打开时的背景色（默认#2979ff）
 * @property {Boolean} inactive-color 关闭时的背景色（默认#ffffff）
 * @event {Function} change 在switch打开或关闭时触发
 * @example <u-switch v-model="checked" active-color="red" inactive-color="#eee"></u-switch>
 */
export default {
	name: "u-switch",
	props: {
		// 是否为加载中状态
		loading: {
			type: Boolean,
			default: false
		},
		// 是否为禁用装填
		disabled: {
			type: Boolean,
			default: false
		},
		// 开关尺寸，单位rpx
		size: {
			type: [Number, String],
			default: 50
		},
		// 打开时的背景颜色
		activeColor: {
			type: String,
			default: '#2979ff'
		},
		// 关闭时的背景颜色
		unactiveColor: {
			type: String,
			default: '#ffffff'
		},
		// 通过v-model双向绑定的值
		value: {
			type: Boolean,
			default: false
		},
		// 是否使手机发生短促震动，目前只在iOS的微信小程序有效(2020-05-06)
		vibrateShort: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {

		}
	},
	computed: {
		switchStyle() {
			let style = {};
			style.fontSize = this.size + 'rpx';
			style.backgroundColor = this.value ? this.activeColor : this.unactiveColor;
			return style;
		},
		loadingColor() {
			return this.value ? this.activeColor : null;
		}
	},
	methods: {
		onClick() {
			if (!this.disabled && !this.loading) {
				// 使手机产生短促震动，微信小程序有效，APP(HX 2.6.8)和H5无效
				if(this.vibrateShort) uni.vibrateShort();
				this.$emit('input', !this.value);
				// 放到下一个生命周期，因为双向绑定的value修改父组件状态需要时间，且是异步的
				this.$nextTick(function() {
					this.$emit('change', this.value);
				})
			}
		}
	}
};
